<div class="filter"><div class="filter__header" @click="toggleMenu" v-if="isMobileView"></div><div class="filter__overlay" v-if="isMenuOpen" @click="closeMenu"></div><transition name="slide-up"><div class="filter__list" v-if="!isMobileView || isMenuOpen" @touchstart="handleTouchStart" @touchmove="handleTouchMove"><label class="filter__label" v-for="(label, filter) in filterLabels" :key="filter"><ToggleSwitch :modelValue="filters[filter]" @update:modelValue="value =&gt; toggleFilter(filter, value)"></ToggleSwitch><span>{{ label }}</span></label></div></transition></div>