<li class="product-card"><a class="product-card__link"><div class="product-card__image"><img :src="product.imageUrl" :alt="product.name"></div><div class="product-card__description"> <h3 class="product-card__title">{{ product.name }}</h3><p class="product-card__article">: {{ product.sku }}</p></div><div class="product-card__container"><p class="product-card__price">: {{ product.price }} </p><button class="product-card__add-to-cart" @click="addToCart" @mouseover="hover = true" @mouseleave="hover = false">+</button></div></a></li>