<section class="hero"><HeroBreadcrumbs class="hero__breadcrumbs" :breadcrumbs="['', '', '']"></HeroBreadcrumbs><div class="hero__slider"><div class="hero__slides" :style="{ transform: `translateX(-${currentSlide * 100}%)` }" @touchstart="handleTouchStart" @touchmove="handleTouchMove" @touchend="handleTouchEnd"><div class="hero__slide" v-for="(slide, index) in slides" :key="index"><img class="hero__image" :src="slide.image" :alt="slide.alt" :width="slide.width" :height="slide.height"><div class="hero__text"><h2 class="hero__title">{{ slide.title }}</h2><p class="hero__description">{{ slide.description }}</p></div></div></div></div><div class="hero__controls"><button class="hero__button hero__button--prev" @click="prevSlide"><span class="visually-hidden"> </span><svg width="20" height="40"><use xlink:href="#icon-slider_button"></use></svg></button><button class="hero__button hero__button--next" @click="nextSlide"><span class="visually-hidden"> </span><svg width="20" height="40"><use xlink:href="#icon-slider_button"></use></svg></button></div><div class="hero__pagination"><span class="hero__dot" v-for="(slide, index) in slides" :key="index" :class="{ 'active': index === currentSlide }" @click="goToSlide(index)"></span></div></section>