<ul class="cart-modal__list"><li class="cart-modal__item" :class="{ 'cart-modal__item--removed': item.removed }" v-for="item in cartItems" :key="item.product.id"><img class="cart-modal__item-image" :src="item.product.imageUrl" :alt="item.product.name"><div class="cart-modal__item-details"><h3 class="cart-modal__item-name"> <a class="cart-modal__item-link">{{ item.product.name }}</a></h3><p class="cart-modal__item-price">{{ item.product.price }} </p></div><div class="cart-item__controls"><button class="cart-item__control-button" :disabled="item.removed" @click="decreaseQuantity(item.product.id)">-</button><span class="cart-item__quantity">{{ item.quantity }}</span><button class="cart-item__control-button" :disabled="item.removed" @click="increaseQuantity(item.product.id)">+</button></div><div class="cart-item__action"><button class="cart-item__remove" v-if="!item.removed" @click="removeItem(item.product.id)"></button><div class="cart-item__icon-container" v-if="item.removed" @click="restoreItem(item.product.id)">             <span class="visually-hidden"></span><svg class="cart-item__icon" width="24" height="24"><use xlink:href="#icon-repeat"></use></svg></div></div><div class="cart-item__overlay" v-if="item.removed"></div></li></ul>